import request from '@/static/request.js'
export default {
  onLoad(options) {
	  uni.getStorage({
	    key: 'ActivityId',
	    success: res => {
	      let self = this
	      self.id = res.data
	    }
	  })
    uni.getStorage({
      key: 'GetId',
      success: res => {
        let self = this;
        self.userId = res.data;
      }
    })
  },
  data() {
    return { 
      id: '',
      userId: '',
      number: '0',
      name: '',
      startTime: '',
      endTime: '',
      location: '',
      abstract: '',
      peopleneed: '',
      tips: '',
      status: '',
      items: '',
      ifSignup: '',
    };
  },
  methods: {
    apply() {
      const vm = this
      request
        .get('attendActivity/', {
          attender_id: vm.userId,
          activity_id: vm.id
        })
        .then(res => {
          if(res.data.statuscode === "200") {
            uni.showToast({
              title: "报名成功！",
              duration: 1000
            });
          }
        })
    },
    ifFull() {
      const vm = this
      if(vm.number < vm.peopleneed)
      {
        return false
      } else {
        return true
      }
    }
  },
  mounted() {
	  const vm = this
	  let time = []
    request
      .post('specificActivityMsg/', {
        id: vm.id,
      })
      .then(res => {
        vm.number = res.data[1]
        vm.name = res.data[0].activity_name
        time = (res.data[0].activity_time).split(',')
        vm.startTime = time[0]
        vm.endTime = time[1]
        let start = new Date(Date.parse(vm.startTime))
        let end = new Date(Date.parse(vm.endTime))
        let now = new Date()
        if(now < start) {
          vm.status = '未开始'
        } else if(now >= start && now <= end) {
          vm.status = '进行中'
        } else {
          vm.status = '已结束'
        }
        vm.location = res.data[0].activity_location
        vm.abstract = res.data[0].activity_abstract
        vm.peopleneed = res.data[0].activity_peopleneed
        vm.tips = res.data[0].activity_tips
        vm.items = res.data[0].activity_items
      })
    request
      .get("ifinActivity/", {
        attender_id: vm.userId,
        activity_id: vm.id
      })
      .then(res => {
        vm.ifSignup = res.data.statuscode === "200";
      });
  },
};
